<h2 id="chang-wen-ben"><a class="header-anchor" href="#chang-wen-ben" aria-hidden="true">¶</a> 长文本</h2>
<demo-block>
                            <xl-tooltip :width="{min:150,max:300}" content="史蒂夫·乔布斯（Steve Jobs），1955年2月24日生于美国加利福尼亚州旧金山，美国发明家、企业家、美国苹果公司联合创办人。">
    <xl-button>长文本</xl-button>
</xl-tooltip>

                            <div slot="desc"><p>通过指定<code>width</code>改变弹出框的长度</p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">xl-tooltip</span> <span class="hljs-attr">:width</span>=<span class="hljs-string">"{min:150,max:300}"</span> <span class="hljs-attr">content</span>=<span class="hljs-string">"史蒂夫·乔布斯（Steve Jobs），1955年2月24日生于美国加利福尼亚州旧金山，美国发明家、企业家、美国苹果公司联合创办人。"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-button</span>&gt;</span>长文本<span class="hljs-tag">&lt;/<span class="hljs-name">xl-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">xl-tooltip</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="biao-ti-he-nei-rong-zhi-chi-html"><a class="header-anchor" href="#biao-ti-he-nei-rong-zhi-chi-html" aria-hidden="true">¶</a> 标题和内容支持html</h2>
<demo-block>
                            <xl-tooltip dangerous-html="" content="<b>这是加粗的内容</b>">
    <xl-button>富文本</xl-button>
</xl-tooltip>

                            <div slot="desc"><p>通过指定<code>dangerousHtml</code>支持HTML</p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">xl-tooltip</span> <span class="hljs-attr">dangerous-html</span> <span class="hljs-attr">content</span>=<span class="hljs-string">"&lt;b&gt;这是加粗的内容&lt;/b&gt;"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-button</span>&gt;</span>富文本<span class="hljs-tag">&lt;/<span class="hljs-name">xl-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">xl-tooltip</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="yan-chi-xian-shi"><a class="header-anchor" href="#yan-chi-xian-shi" aria-hidden="true">¶</a> 延迟显示</h2>
<demo-block>
                            <xl-tooltip content="显示消息" :delay="1000">
    <xl-button>延迟1s显示</xl-button>
</xl-tooltip>

                            <div slot="desc"><p>通过指定<code>dealy</code>延迟显示</p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">xl-tooltip</span> <span class="hljs-attr">content</span>=<span class="hljs-string">"显示消息"</span> <span class="hljs-attr">:delay</span>=<span class="hljs-string">"1000"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-button</span>&gt;</span>延迟1s显示<span class="hljs-tag">&lt;/<span class="hljs-name">xl-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">xl-tooltip</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="zong-shi-xian-shi"><a class="header-anchor" href="#zong-shi-xian-shi" aria-hidden="true">¶</a> 总是显示</h2>
<demo-block>
                            <xl-checkbox v-model="always" label="是否总显示"></xl-checkbox>
<xl-checkbox v-model="disabled" label="是否总不显示"></xl-checkbox>
<br>
<br>
<xl-tooltip content="显示消息" :always="always" :disabled="disabled">
    <xl-button>{{disabled?"禁止提示框":always?"总是显示提示框":"鼠标悬浮显示"}}</xl-button>
</xl-tooltip>

                            <div slot="desc"><p>通过指定<code>always</code></p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">xl-checkbox</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"always"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"是否总显示"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">xl-checkbox</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"disabled"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"是否总不显示"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">br</span>/&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">br</span>/&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">xl-tooltip</span> <span class="hljs-attr">content</span>=<span class="hljs-string">"显示消息"</span> <span class="hljs-attr">:always</span>=<span class="hljs-string">"always"</span> <span class="hljs-attr">:disabled</span>=<span class="hljs-string">"disabled"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-button</span>&gt;</span>{{disabled?"禁止提示框":always?"总是显示提示框":"鼠标悬浮显示"}}<span class="hljs-tag">&lt;/<span class="hljs-name">xl-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">xl-tooltip</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="tooltip-shu-xing"><a class="header-anchor" href="#tooltip-shu-xing" aria-hidden="true">¶</a> Tooltip 属性</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>transfer</td>
<td>是否将弹出框置于body中</td>
<td>boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>placement</td>
<td>弹出框相对显示框的位置</td>
<td>string</td>
<td>top/top-start/top-end/bottom/bottom-start/bottom-end/left/left-start/left-end/right/right-start/right-end</td>
<td>bottom</td>
</tr>
<tr>
<td>width</td>
<td>弹出框宽,通过设置{min:10,max:20}来设置最小宽度和最大宽度</td>
<td>string/number/object</td>
<td>-</td>
<td>{min: 150,max: 300}</td>
</tr>
<tr>
<td>content</td>
<td>内容</td>
<td>string</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>padding</td>
<td>自定义边距</td>
<td>string</td>
<td>-</td>
<td>'0.5em,1em'</td>
</tr>
<tr>
<td>dangerous-html</td>
<td>允许嵌入HTML</td>
<td>boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>delay</td>
<td>延迟显示</td>
<td>number(ms)</td>
<td>-</td>
<td>0</td>
</tr>
<tr>
<td>disabled</td>
<td>是否总不显示</td>
<td>boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>always</td>
<td>是否总是显示(<code v-pre="">disabled=false</code>下可用)</td>
<td>boolean</td>
<td>-</td>
<td>false</td>
</tr>
</tbody>
</table>
<h2 id="tooltip-slots"><a class="header-anchor" href="#tooltip-slots" aria-hidden="true">¶</a> Tooltip slots</h2>
<table class="table"><thead>
<tr>
<th>名称</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>default</td>
<td>插入显示框</td>
</tr>
</tbody>
</table>
</section></template>
<style>
    .demo-tooltip{
        margin-bottom:2em;
    }
</style>
<script>
    export default{
        data(){
            return{
                disabled:false,
                always: false
            }
        }
    }
</script>


